import React from 'react';
import { Table } from 'react-bootstrap';
import Latex from 'react-latex';
import { Tex } from 'react-tex';
const bfTex = 'factor(nodo)=altura(der)-altura(izq) \\in \\{-1,0,1\\}';
const complexity = (
  <div className='complexity'>
    <Table striped bordered hover>
      <thead>
        <tr>
          <th>Operacin</th>
          <th>Mejor caso</th>
          <th>Peor caso</th>
          <th>Caso promedio</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>
            Insercin
            </td>
          <td>
            <Latex>$O(1)$</Latex>
          </td>
          <td>
            <Latex>$O(\log n)$</Latex>
          </td>
          <td>
            <Latex>$O(\log n)$</Latex>
          </td>
        </tr>
        <tr>
          <td>
            Bsqueda
            </td>
          <td>
            <Latex>$O(1)$</Latex>
          </td>
          <td>
            <Latex>$O(\log n)$</Latex>
          </td>
          <td>
            <Latex>$O(\log n)$</Latex>
          </td>
        </tr>
        <tr>
          <td>
            Eliminacin
            </td>
          <td>
            <Latex>$O(1)$</Latex>
          </td>
          <td>
            <Latex>$O(\log n)$</Latex>
          </td>
          <td>
            <Latex>$O(\log n)$</Latex>
          </td>
        </tr>
      </tbody>
    </Table>
  </div>
);

const AVL = (
  <div>
    <h1>rbol AVL</h1>
    <div>
      {complexity}
      <p>
        El rbol AVL (por sus publicadores, Adelson-Velskii y Landis) es un rbol binario de bsqueda
        balanceado, lo que le permite mantener una altura logartmica con respecto a la cantidad 
        de elementos almacenados, lo que significa que poseen un peor caso de complejidad
        logartmica en sus operaciones de insercin, eliminacin y bsqueda.
      </p>
    </div>
    <p>
      El rbol AVL se mantiene balanceado <i>por altura</i>. Para cada nodo, 
      la diferencia absoluta de altura entre ambos sub-rboles hijos es a lo sumo 1.
    </p>
    <p>
      Se conoce como <i>factor de balance</i> a la diferencia existente entre altura de hijo derecho
      e izquierdo. Para que un rbol binario de bsqueda sea AVL, se debe cumplir que:
    </p>
    <Tex texContent={bfTex}/>
    <h2>Operaciones</h2>
    <p>
      Los rboles AVL permite las mismas operaciones que las de un rbol binario de bsqueda simple.
      Sin embargo, como las modificaciones potencialmente desbalancearn el rbol, los nodos 
      involucrados en la operacin (incluidos desde la raz hasta el nodo en cuestin) deben ser balanceados
      por medio de <i>rotaciones</i>
    </p>
    <h3>Balancear</h3>
    <p>Un nodo se encuentra desbalanceado si su factor de balance es <i>-2</i> o <i>2</i>.</p>
    <p>Para rebalancear se realizan rotaciones, los cuales no son mas que modificaciones 
      en las que se mantiene el orden de los elementos
    </p>
    <p>Existen dos casos a considerar cuando se balancea un rbol A, los cuales se dividen en dos casos:</p>
    <ul>
      <li key='1'>
        <p><b>Caso 1</b>: El sub-rbol derecho es el mas alto (el factor del rbol es 2) </p>
        <p>Siendo D el sub-rbol derecho, se consideran dos casos:</p>
        <ul>
          <li key='1.1'>
            <p><b>Caso 1.1</b>: El sub-rbol derecho de D es mas alto (el factor de D es 1 o 0)</p>
            <p>En este caso se realiza una rotacin izquierda en A</p>
          </li>
          <li key='1.2'>
            <p><b>Caso 1.2</b>: El sub-rbol izquierdo de D es mas alto (el factor de D es -1)</p>
            <p>En este caso se realizar una <i>doble rotacin</i>, primero se rotar D hacia la derecha y luego
            se rotar A hacia la izquierda</p>
          </li>
        </ul>
      </li>
      <li key='2'>
        <p><b>Caso 2</b>: El sub-rbol izquierdo de A es el mas alto (el factor de A es 2) </p>
        <p>Siendo I el sub-rbol izquierdo, se consideran dos casos:</p>
        <ul>
          <li key='2.1'>
            <p><b>Caso 2.1</b>: El sub-rbol izquierdo de I es mas alto (el factor de I es -1 o 0)</p>
            <p>En este caso se realiza una rotacin derecha en A</p>
          </li>
          <li key='2.2'>
            <p><b>Caso 2.2</b>: El sub-rbol derecho de I es mas alto (el factor de I es 1)</p>
            <p>En este caso se realizar una <i>doble rotacin</i>, primero se rotar I hacia la izquierda y luego
            se rotar A hacia la derecha</p>
          </li>
        </ul>
      </li>
    </ul>

  </div>
)

export default AVL;