import React from 'react';
import { Table } from 'react-bootstrap';
import Latex from 'react-latex';
const complexity = (
  <div className='complexity'>
    <Table striped bordered hover>
      <thead>
        <tr>
          <th>Operacin</th>
          <th>Mejor caso</th>
          <th>Peor caso</th>
          <th>Caso promedio</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>
            Insercin
            </td>
          <td>
            <Latex>$O(1)$</Latex>
          </td>
          <td>
            <Latex>$O(n)$</Latex>
          </td>
          <td>
            <Latex>$O(\log n)$</Latex>
          </td>
        </tr>
        <tr>
          <td>
            Bsqueda
            </td>
          <td>
            <Latex>$O(1)$</Latex>
          </td>
          <td>
            <Latex>$O(n)$</Latex>
          </td>
          <td>
            <Latex>$O(\log n)$</Latex>
          </td>
        </tr>
        <tr>
          <td>
            Eliminacin
            </td>
          <td>
            <Latex>$O(1)$</Latex>
          </td>
          <td>
            <Latex>$O(n)$</Latex>
          </td>
          <td>
            <Latex>$O(\log n)$</Latex>
          </td>
        </tr>
      </tbody>
    </Table>
  </div>
);

const BST = (
  <div>
    <h1>
      rbol Binario de Bsqueda
    </h1>
    <div>
      {complexity}
      <p>
        Un rbol Binario de Bsqueda es una estructura de datos
        en la cual se cumplen las siguientes propiedades:
        <ol>
          <li key='1'>
            Es un rbol Binario
          </li>
          <li key='2'>
            Todos los nodos pertenecientes al sub-rbol izquierdo son menores
            o iguales que la raz
          </li>
          <li key='3'>
            Todos los nodos pertenecientes al sub-rbol derecho son mayores
            que la raz
          </li>
          <li key='4'>
            Todos los nodos del rbol son rboles Binarios de Bsqueda
          </li>
        </ol>
      </p>
    </div>
    <p>
      Al mantener los datos de manera ordenada, los ABB son frecuentemente
      utilizados para eficientes inserciones, bsqueda y eliminaciones.

      Los ABB poseen como peor caso complejidad en tiempo lineal. Es por esto
      que existen varios tipos de ABB que procuran mantener una altura logartmica,
      tales como los <i>AVL</i> o los <i>rojo-negro</i>
    </p>
    <h2>Operaciones</h2>
    <h3>Bsqueda</h3>
    <p>
      Los rboles Binarios de Bsqueda permiten realizar bsqueda binaria
      sobre sus elementos. Para buscar, simplemente se compara el valor del nodo actual
      al buscado. Si el elemento buscado es menor al nodo actual, se busca recursivamente
      en el sub-rbol izquierdo, de lo contrario se busca en el sub-rbol derecho.

      Este proceso se repite hasta encontrar el elemento, o llegar a una hoja del rbol
    </p>

    <h3>Insercin</h3>
    <p>
      La insercin se lleva a cabo de la misma manera recursiva que la bsqueda.
      Al llegar a una hoja, se crea un nuevo nodo y se aade al rbol.
    </p>

    <h3>Eliminacin</h3>
    <p>
      Existen tres casos a considerar en la eliminacin de nodos en un ABB.
      <ol>
        <li key='1'>
          Eliminar una hoja: El caso mas sencillo, simplemente se elimina del rbol. Se sigue manteniendo
          la propiedad del ABB.
        </li>
        <li key='2'>Eliminar un nodo con un solo hijo: En este caso, el nico hijo del nodo a eliminar
          lo suplantar. Llamando P y H a los nodos padre e hijo del nodo a eliminar, se convierte a H
          como el nuevo hijo de P. En el caso de que el nodo a eliminar sea la raz, H se convierte en la nueva
          raz del rbol
        </li>
        <li key='3'>
          <p>
            Eliminar un nodo con dos hijos: Debido a la complejidad de eliminar un nodo con
            dos hijos, este caso se puede simplificar a alguno de los casos anteriores. Para esto, se busca el nodo
          <i>sucesor</i> del nodo a eliminar, es decir, el mnimo nodo en el rbol que sea mayor que
            ste. Para encontrar el sucesor, se debe buscar el nodo mas a la izquierda del hijo derecho.
          </p>
          <p>
            Una vez encontrado el nodo sucesor <i>s</i>, se intercambian los valores y ste
            es eliminado. Es fcil demostrar que s ser una hoja (caso 1) o tendr solo un hijo (caso 2)
          </p>
          <p>
            De manera anloga, este proceso puede ser realizado buscando el nodo <i>predecesor</i>
          </p>
        </li>
      </ol>
    </p>
  </div>
)

export default BST;